@font-face {
  font-family: "Inter";
  font-style: normal;
  font-weight: 400;
  src: url(/mesto-react/static/media/Inter-Regular.dc131113.woff2) format("woff2"), url(/mesto-react/static/media/Inter-Regular.3ac83020.woff) format("woff");
}

@font-face {
  font-family: "Inter";
  font-style: normal;
  font-weight: 500;
  src: url(/mesto-react/static/media/Inter-Medium.75db5319.woff2) format("woff2"), url(/mesto-react/static/media/Inter-Medium.c0638bea.woff) format("woff");
}

@font-face {
  font-family: "Inter";
  font-style: normal;
  font-weight: 900;
  src: url(/mesto-react/static/media/Inter-Black.661569af.woff2) format("woff2"), url(/mesto-react/static/media/Inter-Black.d0b121f3.woff) format("woff");
}

.page {
  background-color: #000;
  font-family: "Inter", "Arial", "sans-serif";
  margin: 0 auto;
  min-height: 100vh;
  -moz-osx-font-smoothing: grayscale;
  -webkit-font-smoothing: antialiased;
  -webkit-text-size-adjust: 100%;
  -ms-text-size-adjust: 100%;
  -moz-text-size-adjust: 100%;
  text-rendering: optimizeLegibility;
}

.container {
  max-width: 1280px;
  min-width: 320px;
  margin: 0 auto;
  box-sizing: border-box;
}

.header {
  max-width: 100%;
  padding: 45px 0 41px 200px;
  margin: 0 auto;
}

@media screen and (max-width: 1024px) {
  .header {
    padding-left: 100px;
  }
}

@media screen and (max-width: 440px) {
  .header {
    padding: 28px 0 32px 27px;
  }
}

.logo {
  background-image: url(/mesto-react/static/media/mesto-logo.43be87ea.svg);
  width: 142px;
  height: 33px;
  background-repeat: no-repeat;
  background-size: contain;
  display: block;
}

@media screen and (max-width: 320px) {
  .logo {
    width: 104px;
    height: 25px;
  }
}

.profile {
  max-width: 880px;
  padding: 40px 0 52px;
  margin: 0 auto;
  display: flex;
  justify-content: space-between;
  align-items: center;
  border-top: 1px solid rgba(84, 84, 84, 0.7);
  box-sizing: border-box;
}

@media screen and (max-width: 960px) {
  .profile {
    flex-direction: column;
    padding: 42px 0 36px;
  }
}

.profile__button-edit-avatar {
  position: relative;
  margin: 0;
  padding: 0;
  width: 120px;
  height: 120px;
  border: none;
  background-color: transparent;
  border-radius: 50%;
  overflow: hidden;
}

.profile__button-edit-avatar::before {
  content: "";
  position: absolute;
  top: 0;
  left: 0;
  background-color: #000;
  width: 100%;
  height: 100%;
  opacity: 0;
  border-radius: 50%;
  transition: opacity 0.3s ease-in-out;
}

.profile__button-edit-avatar:hover::before {
  opacity: 0.8;
  border-radius: 50%;
}

.profile__button-edit-avatar::after {
  content: "";
  position: absolute;
  top: 0;
  left: 0;
  background-image: url(/mesto-react/static/media/icon-edit-avatar.34c46850.svg);
  background-position: center;
  background-repeat: no-repeat;
  opacity: 0;
  width: 100%;
  height: 100%;
  cursor: pointer;
  border-radius: 50%;
  transition: opacity 0.3s ease-in-out;
}

.profile__button-edit-avatar:hover::after {
  opacity: 0.8;
  border-radius: 50%;
}

.profile__avatar {
  width: 100%;
  height: 100%;
  border-radius: 50%;
  display: block;
  object-fit: cover;
  cursor: pointer;
}

@media screen and (max-width: 600px) {
  .profile__avatar {
    margin-right: 0;
    margin-bottom: 26px;
  }
}

@media screen and (max-width: 320px) {
  .profile__avatar {
    margin-bottom: 22px;
  }
}

.profile__info {
  display: flex;
  justify-content: flex-start;
  align-items: center;
}

@media screen and (max-width: 600px) {
  .profile__info {
    flex-direction: column;
    padding-bottom: 30px;
  }
}

.profile__title-block {
  margin-left: 30px;
  width: 337px;
  /* display: flex;
  flex-direction: column;
  position: relative; */
  display: grid;
  grid-template-columns: 1fr 24px;
  grid-template-rows: repeat(2, -webkit-min-content);
  grid-template-rows: repeat(2, min-content);
  grid-template-areas:
    "title button"
    "subtitle subtitle";
  align-items: baseline;
  grid-row-gap: 16px;
  row-gap: 16px;
}

@media screen and (max-width: 600px) {
  .profile__title-block {
    width: 282px;
    grid-template-columns: 1fr 18px;
    align-items: baseline;
    justify-items: center;
    grid-row-gap: 14px;
    row-gap: 14px;
  }
}

.profile__title {
  margin: 0;
  max-width: 294px;
  color: #fff;
  font-size: 42px;
  line-height: 48px;
  font-weight: 500;
  grid-area: title;
}

@media screen and (max-width: 600px) {
  .profile__title {
    max-width: 254px;
    font-size: 27px;
    line-height: 33px;
    justify-self: center;
    margin-right: -18px;
  }
}

.profile__subtitle {
  color: #fff;
  margin: 0;
  font-size: 18px;
  line-height: 22px;
  font-weight: 400;
  grid-area: subtitle;
}

@media screen and (max-width: 600px) {
  .profile__subtitle {
    max-width: 282px;
    font-size: 14px;
    line-height: 17px;
    /* justify-self: center; */
  }
}

.profile__button-edit {
  padding: 0;
  width: 24px;
  height: 24px;
  border: 1px solid #fff;
  background: transparent;
  background-image: url(/mesto-react/static/media/icon-edit.58afbe73.svg);
  background-position: center;
  background-repeat: no-repeat;
  grid-area: button;
  /* position: absolute;
  right: 0;
  top: 15px; */
  cursor: pointer;
  opacity: 1;
  transition: opacity 0.2s linear;
}

.profile__button-edit:hover {
  opacity: 0.6;
}

@media screen and (max-width: 600px) {
  .profile__button-edit {
    /* top: 10px;
    right: 20px; */
    width: 18px;
    height: 18px;
    margin-right: 19px;
  }
}

.profile__button-add {
  width: 150px;
  height: 50px;
  background: transparent;
  border: 2px solid #fff;
  border-radius: 2px;
  background-image: url(/mesto-react/static/media/plus.bdfb8ebb.svg);
  background-position: center;
  background-repeat: no-repeat;
  padding: 0;
  color: #fff;
  cursor: pointer;
  opacity: 1;
  transition: opacity 0.2s linear;
}

.profile__button-add:hover {
  opacity: 0.6;
}

@media screen and (max-width: 600px) {
  .profile__button-add {
    width: 282px;
  }
}

.overflow {
  text-overflow: ellipsis;
  white-space: nowrap;
  overflow: hidden;
}

.elements {
  max-width: 880px;
  margin: 0 auto;
}

.card {
  display: grid;
  grid-template-columns: repeat(3, 1fr);
  grid-auto-rows: 361px;
  grid-column-gap: 17px;
  -webkit-column-gap: 17px;
          column-gap: 17px;
  grid-row-gap: 20px;
  row-gap: 20px;
  list-style: none;
  padding: 0;
  margin: 0 auto;
}

@media screen and (max-width: 940px) {
  .card {
    max-width: calc(17px + 282px * 2);
    grid-template-columns: repeat(2, 1fr);
  }
}

@media screen and (max-width: 650px) {
  .card {
    max-width: 282px;
    grid-template-columns: 1fr;
  }
}

.card__item {
  width: 282px; /*высоту лучше не задавать, она будет зависеть от количества контента внутри контейнера.*/
  border-radius: 10px;
  background-color: #fff;
  position: relative;
  transition: 0.9s ease-in-out;
}

.card__trash {
  display: none;
  width: 18px;
  height: 19px;
  position: absolute;
  right: 20px;
  top: 20px;
  background-color: transparent;
  border: none;
  background-image: url(/mesto-react/static/media/icon-trash.48bb334e.svg);
  background-repeat: no-repeat;
  cursor: pointer;
  opacity: 1;
  transition: opacity 0.2s linear;
}

.card__trash:hover {
  opacity: 0.6;
}

.card__trash_active {
  display: block;
}

.card__image {
  display: block;
  width: 100%;
  height: 282px;
  object-fit: cover;
  object-position: center;
  border-radius: 10px 10px 0 0;
  cursor: pointer;
  transition: 0.8s ease-in-out;
}

.card__name {
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 25px 30px 31px 21px;
}

.card__title {
  max-width: 200px;
  font-weight: 900;
  font-size: 24px;
  line-height: 29px;
  color: #000;
  margin: 0;
}

.card__like-icon {
  padding: 0;
  width: 22px;
  height: 19px;
  background: transparent;
  border: none;
  background-image: url(/mesto-react/static/media/heart.cb5f1643.svg);
  object-fit: contain;
  object-position: center;
  cursor: pointer;
  opacity: 1;
  transition: opacity 0.2s linear;
}

.card__like-icon:hover {
  opacity: 0.5;
}

.card__like-icon_active {
  background-image: url(/mesto-react/static/media/heart-active.933106ea.svg);
}

.card__like-counter {
  font-size: 13px;
  line-height: 16px;
  font-weight: 400;
  padding-top: 3px;
  text-align: center;
  color: #000;
}

.footer {
  max-width: 880px;
  padding: 67px 0 60px;
  margin: 0 auto;
}

@media screen and (max-width: 940px) {
  .footer {
    max-width: calc(17px + 282px * 2);
  }
}

@media screen and (max-width: 650px) {
  .footer {
    max-width: 282px;
    padding: 48px 0 36px;
  }
}

.footer__title {
  font-weight: 400;
  font-size: 18px;
  line-height: 22px;
  color: #545454;
  margin: 0;
}

@media screen and (max-width: 400px) {
  .footer__title {
    font-size: 14px;
    line-height: 17px;
  }
}

.popup {
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100vh;
  align-items: center;
  background: rgba(0, 0, 0, 0.5);
  visibility: hidden;
  opacity: 0;
  transition: visibility 0.3s, opacity 0.3s linear;
  display: flex;
}

.popup_type_preview {
  background: rgba(0, 0, 0, 0.9);
}

.popup__close {
  width: 32px;
  height: 32px;
  padding: 0;
  position: absolute;
  right: -40px;
  top: -40px;
  background: transparent;
  background-image: url(/mesto-react/static/media/close-icon.4c6a2169.svg);
  background-repeat: no-repeat;
  background-size: contain;
  border: none;
  cursor: pointer;
  opacity: 1;
  transition: opacity 0.2s linear;
}

.popup__close:hover {
  opacity: 0.6;
}

@media screen and (max-width: 525px) {
  .popup__close {
    width: 20px;
    height: 20px;
    right: 0px;
    top: -36px;
  }
}

.popup__container {
  margin: 0 auto;
  padding: 34px 36px;
  width: 430px;
  box-sizing: border-box;
  /* position: absolute; абсолютное позиционирование работает вместе с display: block для popup
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%) scale(1); */
  display: flex;
  flex-direction: column;
  background-color: #fff;
  box-shadow: 0px 0px 25px rgba(0, 0, 0, 0.15);
  border-radius: 10px;
  position: relative;
}

@media screen and (max-width: 525px) {
  .popup__container {
    width: 350px;
    padding: 25px 22px;
  }
}

@media screen and (max-width: 380px) {
  .popup__container {
    width: 282px;
  }
}

.popup__preview-container {
  position: relative;
  margin: 0 auto;
  max-width: 75vw;
  max-height: 75vh;
  box-sizing: border-box;
  display: flex;
  flex-direction: column;
  flex-shrink: 0;
}

.popup__form {
  box-sizing: border-box;
}

.popup__title {
  font-weight: 900;
  font-size: 24px;
  line-height: 29px;
  color: #000;
  margin: 0;
  padding-bottom: 54px;
}

@media screen and (max-width: 525px) {
  .popup__title {
    font-size: 18px;
    line-height: 22px;
    padding-bottom: 75px;
  }
}

.popup__input {
  margin: 0;
  padding: 0;
  width: 100%;
  height: 27px;
  font-weight: 400;
  font-size: 14px;
  line-height: 17px;
  color: #000;
  border: none;
  border-bottom: 1px solid rgba(0, 0, 0, 0.2);
}

.popup__input:last-of-type {
  margin-bottom: 0;
}

.popup__save {
  margin: 14px 0 2px;
  padding: 0;
  width: 358px;
  height: 50px;
  background: #000;
  border-radius: 2px;
  color: #fff;
  font-weight: 400;
  font-size: 18px;
  line-height: 22px;
  text-align: center;
  border: none;
  cursor: pointer;
  opacity: 1;
  transition: opacity 0.2s linear;
}

.popup__save:hover {
  opacity: 0.8;
}

@media screen and (max-width: 525px) {
  .popup__save {
    width: 238px;
    height: 46px;
    font-size: 14px;
    line-height: 17px;
    margin: 10px 0 0 0;
  }
}

.popup__input-error {
  font-weight: 400;
  font-size: 12px;
  line-height: 15px;
  color: #ff0000;
  padding-top: 5px;
  display: block;
  min-height: 30px;
}

.popup__input_type_error {
  border-bottom: 1px solid #ff0000;
}

.popup__save_disabled {
  background-color: transparent;
  color: rgba(0, 0, 0, 0.2);
  border: 1px solid rgba(0, 0, 0, 0.2);
  cursor: default;
}

.popup__save_disabled:hover {
  opacity: 1;
}

.popup__preview-image {
  max-width: 75vw;
  max-height: 75vh;
  display: block;
  object-fit: cover;
}

.popup__preview-title {
  font-weight: 400;
  font-size: 12px;
  line-height: 15px;
  color: #fff;
  padding-top: 10px;
}

.popup_opened {
  /* display: block; вариант при абсолютном позиционировании формы попап */
  visibility: visible;
  opacity: 1;
}

